		checkDone(value:string, chaToggleb:boolean,objElements:any){
			console.log('CHECK ARTA:',value);
			 
			if(value == 'Yes')
			{				
				this.doneTab= true;
                this.mySTIForm.controls["sti_testing_date"].setValidators(Validators.required);
                this.mySTIForm.controls["sti_result"].setValidators(Validators.required);
			}
			else if(value == 'No')
			{
				this.doneTab= false;
				this.resultsTab= false;
				this.treatmentTab= false;
                objElements =["sti_testing_date","sti_result","next_sti_treatment_date",
                               "treatment_opt","treatmentnot","sti_treatment_date","completed_sti_treatment_date"]

                Object.keys(objElements).forEach((key) => {        
                      
                     let val = objElements[key]
                     this.mySTIForm.controls[val].clearValidators();
                     this.mySTIForm.controls[val].reset();
         
                });
			}

		}


		checkSTI(value:string, chaToggleb:boolean){
			console.log('CHECK STI:',value);

			if(value == 'Yes')
			{
				this.stiTab = true;
                this.mySTIForm.controls["reason"].setValidators(Validators.required);
                this.mySTIForm.controls["screen_done"].setValidators(Validators.required);
			}
			else if(value == 'No')			
			{			
				this.stiTab= false;
                Object.keys(this.mySTIForm.controls).forEach((key) => {
        
                     this.mySTIForm.controls[key].clearValidators();
                     this.mySTIForm.controls[key].reset();         
                });
                
                this.mySTIForm.get('sti_option').setValue("No");
			}

		}

		checkResults(value:string, chaToggleb:boolean,objElements:any){
			console.log('CHECK ARTA:',value);

			if(value == 'Positive')
			{								
				this.resultsTab= true;
				this.notreatmentTab = false;
                this.mySTIForm.controls["treatment_opt"].setValidators(Validators.required);
			}
			else if(value == 'Negative')
			{						
				this.resultsTab= false;
				this.treatmentTab= false;
                this.notreatmentTab = false;				
                this.mySTIForm.controls["screen_done"].setValidators(Validators.required);
                objElements =["next_sti_treatment_date","sti_treatment_date",
                               "completed_sti_treatment_date","treatment_opt","treatmentnot"]

                Object.keys(objElements).forEach((key) => {        
                      
                     let val = objElements[key]
                     this.mySTIForm.controls[val].clearValidators();
                     this.mySTIForm.controls[val].reset();
         
                });
			}

		}


		checkTreatment(value:string, chaToggleb:boolean,objElements:any){
			console.log('CHECK RESULTS:',value);

             objElements =["next_sti_treatment_date","sti_treatment_date",
                           "completed_sti_treatment_date","treatmentnot"]

                Object.keys(objElements).forEach((key) => {        
                      
                     let val = objElements[key]
                     this.mySTIForm.controls[val].clearValidators();
                     this.mySTIForm.controls[val].reset();         
                });

			if(value == 'Yes')
			{						
				this.treatmentTab= true;
                this.notreatmentTab = false;
                this.mySTIForm.controls["sti_treatment_date"].setValidators(Validators.required);
                this.mySTIForm.controls["next_sti_treatment_date"].setValidators(Validators.required);
                this.mySTIForm.controls["completed_sti_treatment_date"].setValidators(Validators.required);
			}
			else if(value == 'No')
			{						
				this.treatmentTab= false;
                this.notreatmentTab = true;
                this.mySTIForm.controls["treatmentnot"].setValidators(Validators.required);
               
			}

		}



		artSubmit(){

			this.artTab = false;
			this.chaTogglec = 'col-md-12';
			this.tbNo.nativeElement.focus();
		}



