import { Component } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { FormGroup, FormControl,Validators,AbstractControl} from '@angular/forms';

@Component({
  templateUrl: 'settings.component.html'
})
export class SettingsComponent {
   
   siteGlobalForm:FormGroup;
   successMessage ="";
   alert: boolean = false;
   public prisons:any= [];

  constructor(private _siteService: AuthService) { }


  ngOnInit() {
          
          const headers = {
                              'Content-Type': 'application/json',
                              'Authorization': localStorage.getItem("token")
                            }
			
         this.siteGlobalForm = this.createFormGroup();

					  this._siteService.getPrisons(headers).subscribe(
         
         (data: any) => {                      


                  for (var i =0; i < data.length;i++)
                     {
                     	 if (data[i].description=="Prison"){
                            this.prisons.push({"Key":data[i].location_id,"Value":data[i].name})
                          }                    	
                     }
                                         
               },
                error => {console.log(error); })
		}

  createFormGroup(): FormGroup{

    return new FormGroup ({

      sitelist: new FormControl(null,Validators.required),
      siteprefix: new FormControl(null,[Validators.required, Validators.minLength(3)]),
      siteuid: new FormControl(null,[Validators.required, Validators.minLength(5)]),
      siteid: new FormControl(),
      siteuuid: new FormControl(),
      siteprefix_radio: new FormControl(),
      
    })
  }

  isValid(controlName) {

			return this.siteGlobalForm.get(controlName).invalid && this.siteGlobalForm.get(controlName).touched;
		}

   configGlobal(){

                   
        	    
        	    //const selsiteprefix_radio = this.siteGlobalForm.get('siteprefix_radio').value;        	    
        	    //const siteprefix = this.siteGlobalForm.get('siteprefix').value;
              const sitelist = this.siteGlobalForm.get('sitelist').value;
              const selsiteid = this.siteGlobalForm.get('siteid').value;

                if(selsiteid==true){

                       let str =sitelist.split("-")
                       let location_id = str[0] 
                       let location_name =str[1]
                       let site_prefix =location_name.substring(0,3).toUpperCase();

                	  var gvalues1 ={ "property":"current_health_center_id",
                	                  "property_value":location_id }

                    let gvalues2 ={ "property":"current_health_center_name",
                                    "property_value":location_name
                                  }
                    var gvalues3 ={ "property":"site_prefix",                      
                                     "property_value":site_prefix
                                  }

                  
                  for (var i=1; i<=3; i++)
                   {
                      switch (i) {
                                   case 1:
                                    this.AddGlobalProperty(gvalues1);
                                   break;
                                   case 2:
                                    this.AddGlobalProperty(gvalues2);
                                   break;
                                   case 3:
                                   this.AddGlobalProperty(gvalues3);
                                   break;
                                }
                   }

                
                }


       }


     AddGlobalProperty(gvalues){


          	this._siteService.addGlobalProperty(gvalues).subscribe(
			  data => { 
					          console.log("property added "+data);
                    this.alert = true;					         
					     
			          },
				     error => this.successMessage ="Error globla property" )

       }

}


