import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { FormGroup, FormControl,Validators,AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { IDropdownSettings } from 'ng-multiselect-dropdown';

@Component({
  templateUrl: 'register.component.html'
})
export class RegisterComponent implements OnInit {
   
  dropdownList = [];
  selectedItems = [];
  dropdownSettings:IDropdownSettings;
  registerForm: FormGroup;
  alert: boolean = false;
  warning: boolean = false;
  selected_val ="";
  roles =["System admin","Developer","Nurse"]

  constructor(private authService: AuthService, private router: Router,private _activatedroute :ActivatedRoute) { }

  ngOnInit() {

               /*this.dropdownList = [
      { item_id: 1, item_text: 'Mumbai' },
      { item_id: 2, item_text: 'Bangaluru' },
      { item_id: 3, item_text: 'Pune' },
      { item_id: 4, item_text: 'Navsari' },
      { item_id: 5, item_text: 'New Delhi' }
    ];*/

    this.selectedItems = [
      //{ item_id: 3, item_text: 'Pune' },
      //{ item_id: 4, item_text: 'Navsari' }
    ];


    this.dropdownSettings= {
      singleSelection: false,
      //idField: 'item_id',
      //textField: 'item_text',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 5,
      allowSearchFilter: true
    };
  

             this.registerForm = this.createFormGroup();       

             this.authService.getRoles().subscribe(
         
         (data: any) => {                   
                           
                              const roles =data.map(x => x.role); 
                             this.dropdownList =roles                                         
               },
                error => {console.log(error); })

		}

  createFormGroup(): FormGroup{

    return new FormGroup ({

      given_name: new FormControl(null,[Validators.required, Validators.minLength(2)]),
      family_name: new FormControl(null,[Validators.required, Validators.minLength(2)]),
      username: new FormControl(null,Validators.required),
      password: new FormControl(null,[Validators.required, Validators.minLength(6)]),
      cnfpass: new FormControl(null,this.passValidator),
      roles: new FormControl([]),
    })
  }

  function (){

    this.registerForm.controls.password.valueChanges.
    subscribe(
                   x => this.registerForm.controls.cnfpass.updateValueAndValidity()
             )
  }

  isValid(controlName) {

    return this.registerForm.get(controlName).invalid && this.registerForm.get(controlName).touched;
  }
  

  passValidator(control: AbstractControl){

    if (control && (control.value !==null || control.value !== undefined)){

      const cnfpassValue = control.value;

      const passControl = control.root.get("password");

      if (passControl){

        const passValue = passControl.value;

        if (passValue !==cnfpassValue || passValue ===""){

          return {isError :true};
        }
      }
    }

    return null;

  }




  register(){

     this.authService.submitRegister(this.registerForm.value).
     subscribe(
       data => {

                    console.log(data); 
                this.alert = true;
                 this.registerForm.reset();
         setTimeout(() => {
                   //this.router.navigate(['../dashboard'],{relativeTo:this._activatedroute});
         }, 5000);  //5s

       },
       error => {
                   this.warning = true;
                   console.log('register_error', error); 
                    setTimeout(() => { 
                                           this.warning = false;
                                     }, 3000);  //5s
               }
       )
  
         }


  onItemSelect(item: any) {
    console.log(item);
  }
  onSelectAll(items: any) {
    console.log(items);
  }

}


