     UpdateEditedObs(data){
			
			this._patientservice.updateObs(data).subscribe(
			(data: any)=> { 
					             if(this.toasterid == 0){ this.showToasterSuccess("Inmate data saved!!!"); this.toasterid = 1 }           					      
				           },
				error => this.successMessage ="Error user not registered",

				)
		}

	  SaveObsEncounter(data){
           
           this._patientservice.saveObs(data).subscribe(
                   (data: any) => { 
                                      console.log("Observation saved "+data);
                                      if(this.toasterid == 0){ this.showToasterSuccess("Inmate data saved!!!"); this.toasterid = 1 }                                                      
                            },
                            error => this.successMessage ="Error Observations not saved")
      }


       showToasterSuccess(msag){

      this.notifyService.showSuccess(msag, "Successful")

        setTimeout(() => {                                          
                            this.router.navigate(['/base/cards']);

                        }, 3000);
  }


