import { Component } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { SharedService } from '../../services/shared.service';
import { FormGroup, FormControl,Validators,AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DatePipe } from '@angular/common'
import { LoadingController } from '@ionic/angular';

@Component({

     selector: 'app-tables',
  templateUrl: 'tables.component.html'

})
export class TablesComponent {

  state ="active"
  patientId:string="";
  searchValue:any;
  public elements:any= [];
  key:any= "id";
  reverse:boolean=false;
  p:number=1;
  paginate:any={itemsPerPage:10,currentPage:this.p}
  page_count:number = 0;
  ItemsCount:number = 0;

  constructor(private router: Router,private _siteService: AuthService, private shared: SharedService,public datepipe: DatePipe,public loadingController: LoadingController) { }

  
ngOnInit() {

    
     this.loadingController.create({ message:"Loading inmates....."}).then(a => {

                        a.present().then(async b => {   
                                                        let gdata:any = await this.getDatasets()                                                        
                                                        setTimeout(() => {
                                                       
                                                                            if(gdata == 1) { a.dismiss()  }
                     
                                                                         }, 800);  
                                                    });
            });
           
   }

 
 getDatasets(): Promise<string> {             
           
           return new Promise(resolve => {

                let numb: any= this.getPage()
                resolve("1");       
         }); 
                                    
   }



  getPage() {           


      this._siteService.getPrisoners({"page_number":this.page_count}).subscribe(
         
         async (data: any) => {                            

                           this.ItemsCount = data.length;
                           this.page_count = this.page_count+1;

                           data.reverse();   

                  for (var i =0; i < data.length; i++)
                     {
                              
                      var person_id=0,cell1='', cell2='',cell3='',cell4='', cell5 ='',cell6 ="", cell7 ='', cell8 ='',cell9='', cell10='';

                     	  const array =  data[i]['person'];

                                    cell3 = array.birthdate;
                                    cell4 = array.gender;
                                person_id = array.person_id;
                        var active_status = array.dead;

                            
                           const identarry =  data[i]['patient_identifiers'];

                           if (identarry.length == 2){ cell6 = identarry[1].identifier }else{ cell6 = identarry[0].identifier  }

                        if (active_status == 0){
                            
                            Object.keys(array["names"]).map(function(t){  
          
                                     cell1 = array["names"][t].given_name;
                                     cell2 = array["names"][t].family_name; 
                                                                                
                                });                            

                             const attributes =  array['person_attributes'];                               

                          Object.keys(attributes).map(function(t){

                                     const attr = attributes[t].type.name;

                                     if (attr =="Current Place Of Residence"){

                                     	cell5 = attributes[t].value;
                                     }
                                   else if (attr =="Prisoner gender"){

                                     	cell4 = attributes[t].value;
                                     }
                                    else if (attr =="Entry Date"){

                                     	cell8 = attributes[t].value;
                                     }
                                    else if (attr =="Registration Type"){

                                     	let str = attributes[t].value;
                                       cell10 = str.charAt(0).toUpperCase() + str.substring(1)
                                     }
                                    else if (attr =="Cell Number"){

                                     	cell7 = attributes[t].value;
                                     }
                                    else if (attr =="Registration date"){

                                     	cell9 = attributes[t].value;
                                     }         
                                });

                     this.elements.push({"fname":cell1,
                                         "lname":cell2,
                                         "dob":this.datepipe.transform(cell3,"d-MMM-y"),
                                         "residence":cell4,
                                         "gender":cell5,
                                         "jnumber":cell6,
                                         "edate":cell7,
                                         "regtype":this.datepipe.transform(cell8,"d-MMM-y"),
                                         "cellno":this.datepipe.transform(cell9,"d-MMM-y"),
                                         "regdate":cell10,
                                         "patientid":person_id
                                      })

                            }
                   	   }
                                   

                  if (this.ItemsCount ==10){ this.getPage() }
                   
                                         
               },
                error => { console.log(error); })
		}


  setCurrent(page: number) {
      
      this.p = page;
      this.paginate={itemsPerPage:10,currentPage:this.p}          

  }

  getPrevious(page: number){

      this.p = page-1;
      this.paginate={itemsPerPage:10,currentPage:this.p}

    }
	
 getNext(page: number){

      this.p = page+1;
      this.paginate={itemsPerPage:10,currentPage:this.p}

    }

    pageRedirect(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/cards']);

                     }

    prisonerExit(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/prisonexit']);

                     }
    prisonerEdit(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/demographics']);

                     }

     prisonerVisit(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/visitregister']);

                     }

      Search(searchV: string){


           this.searchValue =searchV;


         if( this.searchValue == "" ){                 
           
           this.page_count = 0;
           this.ItemsCount = 0;
           this.p = 1;
           this.elements =[];    
           this.ngOnInit()


         }else{    
               
                this.elements = this.elements.filter(res =>{
                 
                 if (res.fname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )){

                     return res.fname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )

                 }else if (res.lname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )){

                        return res.lname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )
                 }
                 
              })
         }    
       }

    
    sort(key){

        this.key = key;
        this.reverse = !this.reverse;
    }

 

}




  