 populateData(data){


            let dataset = data.reporting;


          for (var i =0; i < dataset.length; i++)
          { 

                    for (var t =0; t < this.DataObject.length; t++)
                    {                        
                        let str = this.DataObject[t].age_group;
                        let gndr = this.DataObject[t].gender;
                        let keys =str.split("-");   
                        let min =keys[0];
                        let max =keys[1];
                        var x = data.age;
                        let range =((x-min)*(x-max) <= 0);

                        if(range ==true && gndr ==data.gender){                                  
                            
                            if (dataset[i].Value !=0 && dataset[i].Value !=undefined){

                                let key = dataset[i].Key;
                                let pvalue = this.DataObject[t][key]; 
                                this.DataObject[t][key] = pvalue + dataset[i].Value;
                                let totalv = this.DataObject.find(x=>x.age_group == "Total")[key];
                                this.DataObject.find(x=>x.age_group == "Total")[key] = totalv + dataset[i].Value;
                            }                          
                                              
                        }                                                                          
                   }
          }
}

   GenerateData(){

               this.selected_date =[]
               this.page_count= 0;
               this.ItemsCount = 0;

               this.DataObject.map(function(item){ 

                  for (let value in item) {

                        let vals = item[value];                             
                        if (Number(vals)){  item[value] = 0  }
   
                     }
               })             


                let start = this.myReports.get('start').value;
                let end = this.myReports.get('end').value;

                let startsmonth = this.datepipe.transform(start,"MMM-y");
                let startsyear = this.datepipe.transform(start,"y");

                let endsmonth = this.datepipe.transform(end,"MMM-y");
                let endsyear = this.datepipe.transform(end,"y");

                let statdate = this.datepipe.transform(start,"y-MM-d");
                let enddate = this.datepipe.transform(end,"y-MM-d");              

                this.selected_date.push(statdate)
                this.selected_date.push(enddate)

                const oneDay = 24 * 60 * 60 * 1000; // hours*minutes*seconds*milliseconds
               const diffDays = Math.round(Math.abs((start - end) / oneDay));

                   this.month = startsmonth+" to "+endsmonth
                  this.year = startsyear+" to "+endsyear

                if (diffDays > 31){
                   
                   this.report_type ="QUARTELY"  
                   this.report_name ="quartely_report"               
                }
                else{
                    
                  this.report_type ="MONTHLY"
                  this.report_name ="monthly_report" 
                }

              this.getLoader()  

      }

    downloadCsvData(){

        this.downloadFile(this.DataObject);

    }

    downloadFile(data, filename = 'data') {
    //let facility =
    let filenames =this.prison_name+"_"+this.report_name
    let arrHeader = ["age_group","gender","ppltn","newp","neweptv","newepttm","newtstd","tstdpstv","allcumptv","precumartsts","allnongtv","eltest","eltesthiv",
                     "eltestpstv","linktocare","elgbtb","inpstb","inxray","ingxpert","inmicroxpy","xyfdtb","xprtfdtb","microxpyfd","tbtreatmt","dtgcount","aliveart","elvload","samplctd",
                     "elgbcvd","psuscovd","testdcv","testptcvd","pisolcvd","precovd","tbhivpstv","allcumartsts","cumipt","cliptmonth","elsti","stiscrn","stisymp","stitreatmnt"];
    let csvData = this.ConvertToCSV(data, arrHeader);
    //console.log(csvData)
    let blob = new Blob(['\ufeff' + csvData], { type: 'text/csv;charset=utf-8;' });
    let dwldLink = document.createElement("a");
    let url = URL.createObjectURL(blob);
    let isSafariBrowser = navigator.userAgent.indexOf('Safari') != -1 && navigator.userAgent.indexOf('Chrome') == -1;
    if (isSafariBrowser) {  //if Safari open in new window to save file with random filename.
      dwldLink.setAttribute("target", "_blank");
    }
    dwldLink.setAttribute("href", url);
    dwldLink.setAttribute("download", filenames+".csv");
    dwldLink.style.visibility = "hidden";
    document.body.appendChild(dwldLink);
    dwldLink.click();
    document.body.removeChild(dwldLink);
  }


  ConvertToCSV(objArray, headerList) {

    //console.log(objArray);
    //console.log(headerList);
    let array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
    let str = '';
    let row = 'S.No,';

    let newHeaders = ["Age Group","Gender","Prison Population","New entrants tracking","New entrants who are Prev. HIV+","New entrants Linked to care","New entrants tested",
                      "New entrants tested HIV positive","Prisoners with Previous HIV Positive Status (cummulative)","Prisoners previously on ART (cummulative)","Prisoners with Known Negative Status","Prisoners eligible for HIV testing",
                      "Eligible prisoners tested for HIV","Prisoners tested HIV Positive","Prisoners linked to ART","Inmates eligible for TB screening","Inmates with presumptive TB","Inmates investigated for TB by Xray",
                      "Inmates investigated for TB by GeneXpert","Inmates investigated for TB by Microscopy","Inmates with confirmed TB by X-Ray","Inmates with confirmed TB GeneXpert","Inmates with confirmed TB Microscopic",
                      "Inmates wth confirmed TB initiated on TB treatment","Inmates on DTG regimen","Inmates alive on ART","Inmates eligible for Viral Load","Number of VL samples Collected",
                      "Inmates screened for COVID-19","Inmates suspected for COVID-19","Inmates tested for COVID-19","Inmates tested positive for COVID-19","Inmates in isolation","Inmates recovered of COVID-19",
                      "TB cases on ART with documented HIV positive status","Clients on ART (cumulative)","ART Clients previously on IPT (cumulative)","ART clients new on IPT (reporting month)",
                      "Inmates eligible for STI screening","Inmates screened for STI","Inmates symptomatic for STI","Inmates  treated for STI"];

    for (let index in newHeaders) {
      row += newHeaders[index] + ',';
    }
    row = row.slice(0, -1);
    str += row + '\r\n';
    for (let i = 0; i < array.length; i++) {
      let line = (i + 1) + '';
      for (let index in headerList) {
        let head = headerList[index];

        line += ',' + this.strRep(array[i][head]);
      }
      str += line + '\r\n';
    }
    return str;
  }

  strRep(data) {
    if(typeof data == "string") {
      let newData = data.replace(/,/g, " ");
       return newData;
    }
    else if(typeof data == "undefined") {
      return "-";
    }
    else if(typeof data == "number") {
      return  data.toString();
    }
    else {
      return data;
    }
  }


convert ()  {

var data = document.getElementById('mytable');
    html2canvas(data).then(canvas => {

var imgData = canvas.toDataURL('image/jpeg');
var imgWidth = 210.5; 
var pageHeight = 295;  
var imgHeight = canvas.height * imgWidth / canvas.width;
var heightLeft = imgHeight;
var doc = new jsPDF('p', 'mm', 'a4');
var position = 10; // give some top padding to first page
let filenames =this.prison_name+"_"+this.report_name
doc.addImage(imgData, 'JPEG', 0, position, imgWidth, imgHeight);
heightLeft -= pageHeight;

while (heightLeft >= 0) {

  position = heightLeft - imgHeight; // top padding for other pages
  doc.addPage();
  doc.addImage(imgData, 'JPEG', 0, position, imgWidth, imgHeight);
  heightLeft -= pageHeight;
}
doc.save(filenames+'.pdf');

})
    }



    

