		getEncouter_Obs(params){
        
           const headers = {'encounter_id': params.encounter_id}

					  this._patientservice.getEncounterObs(headers).subscribe(
         
         (data: any) => { 

                       this.datacount = data.length;

                     for (var i =0; i < data.length;i++)
                     {    
                     	  if(data[i].concept_id==2193){   
                                      this.vaccine_option.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                          	          this.myCovidForm.get("vaccine_option").setValue(data[i].value_text);
                          	          if(data[i].value_text == 'Yes') { this.vaccineYesTab = true;	}
                                  }
                          if(data[i].concept_id==5866){   
                                      this.vaccine_status.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                          	          this.myCovidForm.get("vaccine_status").setValue(data[i].value_text);
                          	          if(data[i].value_text == 'Yes') { this.doneTab = true;	}
                                  }
                          if(data[i].concept_id==5867){  
                                      this.vaccine_type.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                      this.myCovidForm.get("vaccine_type").setValue(data[i].value_text);
                                  }
                           if(data[i].concept_id==1786){  
                                      this.eligible_option.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                      this.myCovidForm.get("done_option").setValue(data[i].value_text);
                                      if(data[i].value_text == 'Yes') { this.doneTab= true;}else{ this.doneTab= false;}
                                  }
                          if(data[i].concept_id==1837){ 
                                     this.covid_test_date.push({"obs_id":data[i].obs_id,"value":data[i].value_datetime.split('T')[0]})
                          	         this.myCovidForm.get("covid_testing_date").setValue(data[i].value_datetime.split('T')[0])
                                  }
                          if(data[i].concept_id==2429){ 
                                     this.test_reasons.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                     this.myCovidForm.get("reasons").setValue(data[i].value_text);
                                  }
                          if(data[i].concept_id==6235){ 
                                    this.test_comorbidities.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                    this.myCovidForm.get("comorbidities").setValue(data[i].value_text);
                                 }
                          if(data[i].concept_id==973){ 
                                    this.covid_symptons.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                          	        this.myCovidForm.get("covid_symptoms").setValue(JSON.parse(data[i].value_text));                          	         
                                }
                           if(data[i].concept_id==1481){ 
                                    this.covid_test.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                    this.myCovidForm.get("covidtest").setValue(data[i].value_text);
                                     if(data[i].value_text == 'Yes') { this.testResultsTab= true;}else{ this.testResultsTab= false;}
                                 }
                          if(data[i].concept_id==2169){ 
                                    this.test_results.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                    this.myCovidForm.get("results").setValue(data[i].value_text); 
                                    if(data[i].value_text == 'Positive') { this.intervationTab = true; 
                                                                           this.InterTab= true; }else{ this.intervationTab= false; }                         	         
                                }
                           if(data[i].concept_id==1185){ 
                                    this.treatment_types.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                    this.intervation=data[i].value_text;
                                    this.myCovidForm.get("intervation_opt").setValue(data[i].value_text) 
                                }
                          //if(data[i].concept_id==1484){ 
                                   // this.treatment_state.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                   // this.myCovidForm.get("treatmentState").setValue(data[i].value_text) 
                                   // if(data[i].value_text == 'Yes') { this.completeTab= true;}else{ this.completeTab= false;}                         	         
                                //}
                           if(data[i].concept_id==3032){ 
                                    this.treatment_intervation.push({"obs_id":data[i].obs_id,"value":data[i].value_datetime.split('T')[0]})
                                    this.myCovidForm.get("outcome_date1").setValue(data[i].value_datetime.split('T')[0]);                          	         
                                }
                            if(data[i].concept_id==1429){ 
                                    this.hospitalization.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                    this.myCovidForm.get("hospitalization").setValue(data[i].value_text)                          	         
                                }
                            if(data[i].concept_id==7195){ 
                                    this.oxygen_option.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                    this.myCovidForm.get("oxygen_option").setValue(data[i].value_text);                          	         
                                }
                            if(data[i].concept_id==6538){ 
                                    this.patient_outcome.push({"obs_id":data[i].obs_id,"value":data[i].value_text})
                                    this.myCovidForm.get("patient_outcome").setValue(data[i].value_text)                          	         
                                }
                            if(data[i].concept_id==9794){ 
                                    this.outcome_date.push({"obs_id":data[i].obs_id,"value":data[i].value_datetime.split('T')[0]})
                                    this.myCovidForm.get("outcome_date").setValue(data[i].value_datetime.split('T')[0])                         	         
                                }
                            if(data[i].concept_id == 2485){ 
                                    this.entry_reason.push({ "obs_id":data[i].obs_id,"value":data[i].value_text })
                                    this.myCovidForm.get("reason").setValue(data[i].value_text)                                      
                                }
                     }     
                                         
               },
                error => {console.log(error); })

		}


