			    this.completeTab =true;	
                this.myCovidForm.controls["outcome_date1"].setValidators(Validators.required);                
                this.myCovidForm.controls["hospitalization"].setValidators(Validators.required);
                this.myCovidForm.controls["intervation_opt"].setValidators(Validators.required);
                //this.myCovidForm.controls["oxygen_option"].setValidators(Validators.required);
