	toggleCollapse(): void {
		this.isCollapsed = !this.isCollapsed;
		this.iconCollapse = this.isCollapsed ? 'icon-arrow-down' : 'icon-arrow-up';
	}

	onOptionsSelected(value:string){
		console.log("the selected value is " + value);
		switch (value) {
			case "KP":
			this.chaToggle = 'KP';
			break;
			case "KN":
			this.chaToggle = 'KN';
			break;
			case "UK":
			this.chaToggle = 'UK';
			break;

			default:
		     		// code...
		     		break;
		     	}

		     }


		 onOptionRegType(value:string){
		console.log("the selected value is " + value);
		switch (value) {
			case "Staff":
			this.regTypeTab= false;	
			this.hiv_alert='';	
			this.identifier ="Employment"				
			break;
			default:
		  this.regTypeTab= true;
		  this.hiv_alert='at entry';
		  this.identifier ="Prison"	
		     		break;
		     	}

		     }	


