	toggleCollapse(): void {
		this.isCollapsed = !this.isCollapsed;
		this.iconCollapse = this.isCollapsed ? 'icon-arrow-down' : 'icon-arrow-up';
	}

	onOptionsSelected(value:string){

		switch (value) {
			case "NP":
			this.nextdateTab = false;
			this._patientservice.setModule("ART");	
			this.myHTSForm.controls["next_hts_appointment_date"].clearValidators();		
			break;
			case "NN":
            let today:any = new Date();
			var newDate = new Date(today.setMonth(today.getMonth()+3));	
			var nextday =this.datepipe.transform(newDate,"y-MM-d")	
			this.nextdateTab = true;
			this._patientservice.setModule("NOART");
			this.myHTSForm.controls["next_hts_appointment_date"].setValidators(Validators.required);
			this.myHTSForm.get("next_hts_appointment_date").setValue(nextday);
			break;
			case "IN":
			this.nextdateTab = false;
			this._patientservice.setModule("NOART");
			break;

			default:
		     	this.myHTSForm.controls["next_hts_appointment_date"].clearValidators();
		     		break;
		     	}

		     }


		Reason(value){

		}

		checkDone(value:string, chaToggleb:boolean){
			console.log('CHECK Test Accepted:',value);
			 
			if(value == 'Yes')
			{			
				this.doneTab= true;
				this.myHTSForm.controls["hts_testing_date"].setValidators(Validators.required);
				this.myHTSForm.controls["htsresults"].setValidators(Validators.required);
			}
			else if(value == 'No')
			{
				this.doneTab= false;
				this.resultsTab= false;
				this.nextdateTab= false;
				this.myHTSForm.controls["hts_testing_date"].clearValidators();
				this.myHTSForm.controls["hts_testing_date"].reset();
				this.myHTSForm.controls["htsresults"].clearValidators();
				this.myHTSForm.controls["htsresults"].reset();
				this.myHTSForm.controls["next_hts_appointment_date"].clearValidators();
				this.myHTSForm.controls["next_hts_appointment_date"].reset();
			}

		}


		checkHTS(value:string, chaToggleb:boolean){
			console.log('CHECK Eligible:',value);

			this.myHTSForm.controls["htsreason"].setErrors(null);            
					
			if(value == 'Yes')
			{
				this.stiTab = true;	
				this.myHTSForm.controls["htsreason"].setValidators(Validators.required);
				this.myHTSForm.controls["offered_option"].setValidators(Validators.required);
				this.myHTSForm.controls["concept_option"].setValidators(Validators.required);
			}
			else if(value == 'No'){
				
				this.stiTab = false;
				this.doneTab = false;
				this.nextdateTab =false;
				Object.keys(this.myHTSForm.controls).forEach((key) => {
        
                     this.myHTSForm.controls[key].clearValidators();
                     this.myHTSForm.controls[key].reset();
         
                });
				
				this.myHTSForm.get('hts_option').setValue("No");
			}

		}


		checkedDone(value:string, chaToggleb:boolean){
			console.log('CHECK TEST OFFERED:',value);				

		}

		checkResults(value:string, chaToggleb:boolean){
			console.log('CHECK ARTA:',value);
			 
			if(value == 'Yes')
			{			
				this.resultsTab= true;
			}
			else if(value == 'No')
			{		
				this.resultsTab= false;
				this.nextdateTab= false;
			}

		}


		checkTreatment(value:string, chaToggleb:boolean){
			console.log('CHECK ARTA:',value);

			if(value == 'Yes')
			{				
				this.nextdateTab= true;
			}
			else if(value == 'No')
			{			
				this.nextdateTab= false;
			}

		}



		artSubmit(){

			this.artTab = false;
			this.chaTogglec = 'col-md-12';
			this.htsNo.nativeElement.focus();
		}



