import { Component } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { SharedService } from '../../services/shared.service';
import { SortPipe } from '../../services/sort.service';
import { FormGroup, FormControl,Validators,AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DatePipe } from '@angular/common'
import { LoadingController } from '@ionic/angular';

@Component({

     selector: 'app-exited',
  templateUrl: 'duehts.component.html'

})
export class DueHtsComponent {

  state ="active"
  patientId:string="";
  searchValue:any;
  public elements:any= [];
  key:any= "id";
  reverse:boolean=false;
  p:number=1;
  paginate:any={itemsPerPage:10,currentPage:this.p}
  page_count:number = 0;
  ItemsCount:number = 0;

  constructor(private router: Router,private _siteService: AuthService, private shared: SharedService,public datepipe: DatePipe, private sortPipe: SortPipe,public loadingController: LoadingController) { }

  ngOnInit() {
                  
                this.loadingController.create({ message:"Loading inmates....."}).then(a => {

                        a.present().then(async b => {   
                                                        let gdata:any = await this.getDatasets()                                                        
                                                        setTimeout(() => {
                                                       
                                                                            if(gdata == 1) { a.dismiss()  }
                     
                                                                         }, 800);  
                                                    });
                              });

             }

getDatasets(): Promise<string> {             
           
           return new Promise(resolve => {

                let numb: any= this.getPage()
                resolve("1");       
         }); 
                                    
   }

  getPage() {           


      this._siteService.getProgrampatients({"page_number":this.page_count}).subscribe(
         
         (data: any) => {                      
                           
                           this.ItemsCount = data.length;
                           this.page_count = this.page_count+1;                          
                            

                  for (var x =0; x < data.length; x++)
                     {                            
                                  
                            let id = data[x].patient_id;


                            if (data[x]['patient_states'].length == 0 ){

                                this._siteService.getPrisonerEncounters({"paginate":false,"patient_id":id}).subscribe(
         
                                 async (data: any) => {                                                       
                                                         

                                                          var persons:any = await this.getPersonAsync(id);                                                                 
                                                    
                                                          var smodules = [{"Key":"HTS","Value":163},{"Key":"STI","Value":146},{"Key":"Cervical Cancer","Value":154}]

                                                    for(let x = 0; x < smodules.length; x++){
                                                     
                                                        var modulevalue = smodules[x].Value 
                                                        var mservice = smodules[x].Key;                                                                                                          
                                                        var dataObject = data.filter( x => x.encounter_type == modulevalue );                                                 

                                                 if ( dataObject.length != 0 ){

                                                        var visitdate = 0;
                                                        var next_visit_date = 0;                                                       
                                                            
                                                          var lastObject = dataObject.slice(-1)                                                          
                                                          Object.keys(lastObject).map(function(t){ 
                                                           
                                                                visitdate = lastObject[t].date_created
                                                                var obs = lastObject[t].observations

                                                                 for (var i =0; i < obs.length; i++)
                                                                 {
                                                                     let conceptid = obs[i].concept_id;
                                                                     if ( conceptid == 5096 ){ next_visit_date = obs[i].value_datetime }
                                                                    
                                                                 }
                                                          });

                        
                            if (next_visit_date != 0){

                                     let oneDay = 24 * 60 * 60 * 1000; 
                                     let today:any = new Date();
                                     let secondDate:any = new Date(next_visit_date);
                                     var daysleft:any = Math.round(Math.abs((today - secondDate) / oneDay));;   
                         
                         if (today <= secondDate){

                            var cell2='',cell3='',cell4='',cell5='';
                          const array =  persons.names
                             var dobs = persons.birthdate
                          
                            Object.keys(array).map(function(t){  
          
                                     cell2 = array[t].given_name;
                                     cell3 = array[t].family_name; 
                                                                                
                                });                            

                             const attributes =  persons.person_attributes;                                                      

                          Object.keys(attributes).map(function(t){

                                     const attr = attributes[t].type.name;

                                     if (attr =="Current Place Of Residence"){

                                        cell4 = attributes[t].value;
                                     }
                                   else if (attr =="Prisoner gender"){

                                        cell5 = attributes[t].value;
                                     }                  
                                            
                                }); 


                                 this.elements.push({"fname":cell2,
                                                     "lname":cell3,
                                                     "dob":this.datepipe.transform(dobs,"d-MMM-y"),
                                                     "residence":cell4,
                                                     "gender":cell5,
                                                     "edate":this.datepipe.transform(visitdate,"d-MMM-y"),
                                                     "regdate":this.datepipe.transform(next_visit_date,"d-MMM-y"),
                                                     "redays":daysleft+1,
                                                     "service":mservice,
                                                     "patientid":id
                                                   })   

                                    } 


                                }
            

                                } 


                                 }

                                 })     



                            }
                           
                    
                   	   }
                                   

                  if (this.ItemsCount ==10){ this.getPage() }



                   
                                         
               },
                error => { console.log(error); })


               
		}

 

getPersonAsync(id: number): Promise<string> {

    return new Promise(resolve => {    


        this._siteService.getPerson(id).subscribe((data: any) => {
            
                resolve(data);

        }, (error: any) => {
            resolve(null);
        });
    });
}



  setCurrent(page: number) {
      
      this.p = page;
      this.paginate={itemsPerPage:10,currentPage:this.p}
  }

  getPrevious(page: number){

      this.p = page-1;
      this.paginate={itemsPerPage:10,currentPage:this.p}

    }
	
 getNext(page: number){

      this.p = page+1;
      this.paginate={itemsPerPage:10,currentPage:this.p}

    }

    pageRedirect(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/cards']);

                     }

    prisonerExit(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/prisonexit']);

                     }

     prisonerVisit(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/visitregister']);

                     }

      Search(searchV: string){


           this.searchValue =searchV;


         if( this.searchValue == "" ){                 
           
           this.page_count = 0;
           this.ItemsCount = 0;
           this.p = 1;
           this.elements =[]    
           this.ngOnInit()


         }else{        
                
                console.log(this.elements)

                this.elements = this.elements.filter( (res ) =>{
                 
                 if (res.fname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )){

                     return res.fname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )

                 }else if (res.lname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )){

                        return res.lname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )
                 
                 }else if (res.redays.toString().toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )){

                        return res.redays.toString().toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )
                 }
                 else if (res.service.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )){

                        return res.service.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )
                 }
                 
              })
         }    
       }

    
    sort(key){

        this.key = key;
        this.reverse = !this.reverse;
    }

 

}




  