import { Component } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { SharedService } from '../../services/shared.service';
import { FormGroup, FormControl,Validators,AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DatePipe } from '@angular/common'

@Component({

     selector: 'app-exited',
  templateUrl: 'exited.component.html'

})
export class ExitedComponent {

  state ="active"
  patientId:string="";
  searchValue:any;
  public elements:any= [];
  key:any= "id";
  reverse:boolean=false;
  p:number=1;
  paginate:any={itemsPerPage:5,currentPage:this.p}
  page_count:number = 0;
  ItemsCount:number = 0;


  constructor(private router: Router,private _siteService: AuthService, private shared: SharedService,public datepipe: DatePipe) { }

  ngOnInit() {
                  
                  this.getPage()                  
             }

  getPage() {           


      this._siteService.getProgrampatients({"page_number":this.page_count}).subscribe(
         
         (data: any) => {                      
                           
                           this.ItemsCount = data.length;
                           this.page_count = this.page_count+1;                      
                            

                  for (var x =0; x < data.length; x++)
                     {                            
                                  
                            let id = data[x].patient_id;

                            if (data[x]['patient_states'].length !=0 ){

                                var exit_reason = data[x].patient_states[0].name
                                var start_date = data[x].patient_states[0].start_date

                       this._siteService.getPerson(id).subscribe(
         
                                 (data: any) => {                                     

                           
                      var person_id=0,cell1='', cell2='',cell3='',cell4='', cell5 ='', cell7 ='',cell8,cell9, cell10='';                          

                            cell3 = data.birthdate;
                            cell4 = data.gender;
                        person_id = data.person_id;

                        const array =  data.names

                          
                            Object.keys(array).map(function(t){  
          
                                     cell1 = array[t].given_name;
                                     cell2 = array[t].family_name; 
                                                                                
                                });                            

                             const attributes =  data.person_attributes;                                                      

                          Object.keys(attributes).map(function(t){

                                     const attr = attributes[t].type.name;

                                     if (attr =="Current Place Of Residence"){

                                        cell5 = attributes[t].value;
                                     }
                                   else if (attr =="Prisoner gender"){

                                        cell4 = attributes[t].value;
                                     }                                  
                                   
                                    else if (attr =="Entry Date"){

                                        cell8 = attributes[t].value;
                                     }
                                    else if (attr =="Registration Type"){

                                        let str = attributes[t].value;
                                       cell10 = str.charAt(0).toUpperCase() + str.substring(1)
                                     }
                                   
                                    else if (attr =="Registration date"){

                                        cell9 = attributes[t].value;
                                     }         
                                });

                     this.elements.push({"fname":cell1,
                                         "lname":cell2,
                                         "dob":this.datepipe.transform(cell3,"d-MMM-y"),
                                         "residence":cell4,
                                         "gender":cell5,
                                         "edate":this.datepipe.transform(cell8,"d-MMM-y"),
                                         "reason":this.datepipe.transform(start_date,"d-MMM-y"),
                                         "regdate":exit_reason,
                                         "patientid":person_id
                                      })                                 
                       
                                

                                },error => {console.log(error); })



                            }
                           
                    
                   	   }
                                   

                  if (this.ItemsCount ==10){ this.ngOnInit() }
                   
                                         
               },
                error => { console.log(error); })
		}


  setCurrent(page: number) {
      
      this.p = page;
      this.paginate={itemsPerPage:5,currentPage:this.p}
  }

  getPrevious(page: number){

      this.p = page-1;
      this.paginate={itemsPerPage:5,currentPage:this.p}

    }
	
 getNext(page: number){

      this.p = page+1;
      this.paginate={itemsPerPage:5,currentPage:this.p}

    }

    pageRedirect(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/cards']);

                     }

    prisonerExit(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/prisonexit']);

                     }

     prisonerVisit(val){

        this.shared.setParameters(val);
        this.router.navigate(['/base/visitregister']);

                     }

      Search(searchV: string){


           this.searchValue =searchV;


         if( this.searchValue == "" ){                 
           
           this.page_count = 0;
           this.ItemsCount = 0;
           this.p = 1;
           this.elements =[]    
           this.ngOnInit()


         }else{        
               
                this.elements = this.elements.filter(res =>{
                 
                 if (res.fname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )){

                     return res.fname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )

                 }else if (res.lname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )){

                        return res.lname.toLocaleLowerCase().match(this.searchValue.toLocaleLowerCase() )
                 }
                 
              })
         }    
       }

    
    sort(key){

        this.key = key;
        this.reverse = !this.reverse;
    }

 

}




  