		async onPresent(chamessage,type) {
		    //this.isLoading = true;
		    return await this.loadingController.create({
		      // duration: 5000,
		      message: chamessage,
		  }).then(a => {
		  	a.present().then(() => {
		  		//console.log('presented');
		        //wait for the dorm to load
		        switch (type) {
		        	case "art":
		        		// art
		        		setTimeout(() => {
				        	if(this.artDummy != null)
				        	{
				        		console.log('zutheka');

				        		a.dismiss().then(() => this.artDummy.nativeElement.focus());

				        		        this.artForm.artFooterTab = false;
		                                this.artForm.artSearchTab = false;
		                                this.artForm.myARTForm.reset();
		                                this.artForm.ViralLoadTab =false;
				        	}
				        	else
				        	{
				        		console.log('zukanika2');
				        	}
				        }, 100);
		        		break;
		        	case "hts":
		        		// art
		        		setTimeout(() => {
				        	if(this.htsDummy != null)
				        	{
				        		console.log('zutheka');

				        		a.dismiss().then(() => this.htsDummy.nativeElement.focus());

				        		this.htsForm.htsFooterTab = false;	
				        		this.htsForm.htsSearchTab = false;

     			        	}
				        	else
				        	{
				        		console.log('zukanika2');
				        	}
				        }, 100);
		        		break;
		        	case "tb":
		        		//tb
		        		setTimeout(() => {
				        	if(this.tbDummy != null)
				        	{
				        		console.log('zutheka TB');

				        		a.dismiss().then(() => this.tbDummy.nativeElement.focus());

				        		     this.tbForm.tbFooterTab=false;
		                     this.tbForm.tbSearchTab=false;

				        	}
				        	else
				        	{
				        		console.log('zukanika TB');
				        	}
				        }, 100);
		        	default:
		        		// code...
		        		break;
		        }
		        

		        //----------
		        
		    });
		  });
		}

     

