       var cancer_testing_date =this.myCancerForm.get('cancer_testing_date').value;
        var screentype =this.myCancerForm.get('screentype').value
       var results =this.myCancerForm.get('results').value;
       var treat_option =this.myCancerForm.get('treat_option').value
       var cxca_treatment_date =this.myCancerForm.get('cxca_treatment_date').value;
       var type_of_treatment = this.myCancerForm.get('type_of_treatment').value;  
       var next_cxca_treatment_date =this.myCancerForm.get('next_cxca_treatment_date').value; 
